import { Avatar, Divider } from 'antd'
import {
    QqOutlined,
    WechatOutlined,
    GithubOutlined
} from '@ant-design/icons';
import './Author.css'

const Author = () => {

    return (
        <div className="author-div comm-box">
            <div> <Avatar size={100} src={require("../../assets/image/blog_backgground.png")} /></div>
            <div className="author-introduction">
                小白搬砖员,专注于WEB和移动前端开发以及嵌入式开发,要录1000集免费视频的傻X。此地维权无门，此时无能为力，此心随波逐流。
                <Divider>社交账号</Divider>

                <Avatar size={28} className="account" ><QqOutlined /></Avatar>
                <Avatar size={28} className="account" ><WechatOutlined /></Avatar>
                <Avatar size={28} className="account" ><GithubOutlined /></Avatar>

            </div>
        </div>
    )

}

export default Author