import React, { useState, useEffect } from 'react';
import './typewriter.css';

const Typewriter = () => {
    const [text, setText] = useState('');
    const [index, setIndex] = useState(0);
    const content = "告别炎炎夏日，初秋清爽迎来。微凉的秋风拂过面颊，带来了一丝丝凉爽与宁静。在这个适合散步的季节里，沐浴着暖阳和清新的空气，让人心情愉悦。路边的枫叶已染上了浅浅的红色，微风吹拂时带起一串串美丽的旋律。静静品味着秋天的美好，感受这次与夏日告别的时刻，感恩一切拥有的美好，期待着更美好的未来。";
    const [showCursor, setShowCursor] = useState(true); // 添加一个变量表示光标是否显示
    const cursorInterval = React.useRef(); // 保存计时器的引用

    useEffect(() => {
        cursorInterval.current = setInterval(() => { // 当打字完成后，光标每隔600毫秒进行一次切换
            setShowCursor(prev => !prev);
        }, 600);

        return () => clearInterval(cursorInterval.current);
    }, []);

    useEffect(() => {
        const timer = setTimeout(() => {
            setText(content.substring(0, index + 1));
            setIndex(index + 1);
        }, 130); // 间隔 100 毫秒打印一个字符

        return () => clearTimeout(timer);
    }, [index, content]);

    return (
        <div className="typewriter-container">
            <span className="typewriter-text">{text}</span>
            {showCursor && <span className="typewriter-cursor">|</span>}
        </div>
    );
}

export default Typewriter;