import React, { useState } from 'react'
import { List, Breadcrumb } from 'antd'
import { Row, Col } from 'antd'
import Header from '../../components/publicHeader/Header.js'
import Author from '../../components/Author/Author.js'
import Footer from '../../components/Footer/Footer.js'
import Advert from '../../components/Advert/Advert.js'
import axios from 'axios'
import { useEffect } from 'react';

import { Link } from 'react-router-dom'; // 如果您使用的是react-router


import "./List.css"





function BlogList() {


    const [mylist, setMylist] = useState(
        [
            { title: '微信【跳一跳】 opencv视觉识别 + 物理外挂', content: '与 opencv视觉库,写了一个跳一跳的物理挂，现在识别率还比较差，先记录下过程，以后在慢慢修改整理。 一、外挂结构 上位机：USB摄像头连接windows电脑，用作处理识别拍摄到图像数据。 下位机：STM32单片机，用于控制陀机附带电容笔进行物理点击。 单片机部分很简单，所以下文主要记录上位机的内容。 二、上位机程序框架' },
            { title: '理解CSS视觉格式化', content: '##前面的话 CSS视觉格式化这个词可能比较陌生，但说起盒模型可能就恍然大悟了。实际上，盒模型只是CSS视觉格式化的一部分。视觉格式化分为块级和行内两种处理方式。理...解视觉格式化，可以确定得到的效果是应该显示的正确效果，还是浏览器兼容性的bug。下面将详细介绍CSS视觉格式化 <p> </p> ###术语解释 了' },
            { title: 'opencv 震撼你的视觉-------基础篇', content: '　最近在做一个钓鱼网站的项目中用到了一个叫opencv的玩意儿，以前没接触过。感觉挺新鲜的，而且项目中要用，所以就问了一下度娘（是想Google一下的，显得高大上and专业一点，但是英语水平实在是low），百度百科的解释是这样的（有点专业，读完以后似懂非懂的感觉，不过觉得这个东西很强势，可以搞一下）：OpenCV的全称是：Open Source Computer Vision Library。OpenCV是一个基于BSD许可（开源）发行的跨平台计算机视觉库，可以运行在Linux、Windows、Android和Mac OS操作系统上。它轻量级而且高效——由一系列 C 函数和少量 C++ 类构成，同时提供了Python、Ruby、MATLAB等语言的接口，实现了图像处理和计算机视觉方面的很多通用算法。' },
            { title: '00后真的是内卷王中王，真的想离职了....', content: '都说00后躺平了，但是有一说一，该卷的还是卷。这不，前段时间我们公司来了个00年的，工作没两年，跳槽到我们公司起薪18K，都快接近我了。后来才知道人家是个卷王，从早干到晚就差搬张床到工位睡觉了。最近和他聊了一次天，原来这位小老弟家里条件不太好，一大家子指望他一个人养活，所以他再大三的时候就已经开始自学软件测试了，大四获得了一个小公司的实习机会。在工作中不断的学习、实践，花的时间比别人多一倍，所以才能打下扎实的基础，现在工作也是得心应手。' },
        ]

    )


    useEffect(() => {
        axios.get('http://127.0.0.1:7001/default/ArticleList').then(res => {
            console.log('---->', res.data);
            setMylist(res.data.data);
        }).catch((err) => {
            console.log(err)
        })
    }, [])



    return (
        <div>
            <Header>
                {/* <title>Home</title> */}
            </Header>
            <Row className='main' type='float' justify="center">
                <Col className="left" xs={24} sm={24} md={16} lg={18} xl={14}  >
                    <div className="bread-div">
                        <Breadcrumb>
                            <Breadcrumb.Item><a href="/bloghome">首页</a></Breadcrumb.Item>
                            <Breadcrumb.Item>相关视频</Breadcrumb.Item>
                        </Breadcrumb>
                    </div>
                    <List
                        header={<div>最新日志</div>}
                        itemLayout="vertical"
                        dataSource={mylist}
                        renderItem={item => (
                            <List.Item>
                                <div className="list-title">
                                    <Link to={{ pathname: '/Detailed', search: `?id=${item.id}` }}>
                                        {item.title}

                                    </Link>
                                </div>
                                <div className="list-icon">
                                    <span>2019-06-28</span>
                                    <span> 视频教程</span>
                                    <span> 5498人</span>
                                </div>
                                <div className="list-context">{item.introduce}</div>
                            </List.Item>
                        )}
                    />
                </Col>

                <Col className="right" xs={0} sm={0} md={7} lg={5} xl={4}>
                    <Author></Author>
                    <Advert></Advert>
                </Col>
            </Row>
            <Footer></Footer>
        </div>
    )
}


export default BlogList;